.PopUp {
  display: flex;
  /* justify-content: center; */
  /* align-items: center; */
  position: absolute;
  top: 15vh;
  height: 45vh;
  width: 25vw;
  background-color: white;
  left: 37%;
  border-radius: 45px 45px 0 0;
  opacity: 1;
  box-shadow: 0 8px 20px 4.5px black;
  z-index: 1;
}

.Toastify__toast-body {
  text-align: center;
}

.heading {
  position: relative;
  margin-top: 1.3vh;
}

.PopUpInside {
  display: flex;
  flex-direction: column;
  position: relative;
  width: 100%;
  height: 100%;
  align-items: center;
  box-shadow: 0 12px 55px 6px black;
  background-color: white;
  border-bottom: 30px solid rgba(211, 84, 0, 1);
  border-radius: 45px 45px 30px 30px;
}

.headingDiv {
  width: 100%;
  background-color: rgba(211, 84, 0, 1);
  border-radius: 45px 45px 0 0;
  text-align: center;
  color: white;
  height: 7vh;
}
.form {
  display: flex;
  flex-direction: column;
  /* border: 1px solid rsed; */
  margin-top: 4vh;
  text-align: center;
  height: 25vh;
}
.inputFields {
  border: none;
  outline: none;
  margin-top: 26px;
  width: 12vw;
  height: 4vh;
  border-radius: 15px;
  text-align: center;
  border: 1.2px solid rgba(230, 126, 34, 1);
  background: none;
}
.inputFields:hover {
  width: 15vw;
  transition: 0.2s;
  border: 1.2px solid rgba(211, 84, 0, 1);
  background: none;
}
.formBtn {
  margin-top: 4vw;
  /* border: 1.2px solid red; */
  height: 5vh;
  width: 7vw;
  border-radius: 15px;
  font-size: 20px;
}
.formBtn:hover {
  background-color: rgba(211, 84, 0, 1);
  cursor: pointer;
  color: white;
  transition: 0.2s;
  border: 1.2px solid white;
  width: 9vw;
}
